<template>
  <div class="left-content" :key="instanceLKey">
    <DeviceStatistics
      class="section-box top-section"
      :monitorSearchInfo="monitorSearchInfo"
      v-if="monitorLoading"
    />
    <GenerationRanking
      class="section-box middel-section"
      :monitorSearchInfo="monitorSearchInfo"
      v-if="monitorLoading"
    />
    <EnergyStorage
      class="section-box bottom-section"
      :monitorSearchInfo="monitorSearchInfo"
      v-if="monitorLoading"
    />
  </div>
  <div class="middle-content" :key="instanceTKey">
    <MiddleIndicator
      icon="active-power"
      label="实时功率(MW)"
      :value="topIndexData.activePower"
      vColor="#67C8FF"
    />
    <MiddleIndicator
      icon="max-active"
      label="峰值功率(MW)"
      :value="topIndexData.maxActive"
      vColor="#F4D038"
    />
    <MiddleIndicator
      icon="today-gen"
      label="今日发电量(万kWh)"
      :value="topIndexData.ppr"
      vColor="#00BA24"
    />
  </div>
  <div class="right-content" :key="instanceRKey">
    <WarningOverview
      class="section-box top-section"
      :monitorSearchInfo="monitorSearchInfo"
      v-if="monitorLoading"
    />
    <RealTimeWarning
      class="section-box middel-section"
      :monitorSearchInfo="monitorSearchInfo"
      v-if="monitorLoading"
    />
    <DispersionRatio
      class="section-box bottom-section"
      :monitorSearchInfo="monitorSearchInfo"
      v-if="monitorLoading"
    />
  </div>
</template>

<script lang="ts" setup>
  import DeviceStatistics from './DeviceStatistics.vue';
  import GenerationRanking from './GenerationRanking.vue';
  import EnergyStorage from './EnergyStorage.vue';
  import MiddleIndicator from '/@/components/Framework/OperationScreen/MiddleIndicator.vue';
  import WarningOverview from './WarningOverview.vue';
  import RealTimeWarning from './RealTimeWarning.vue';
  import DispersionRatio from './DispersionRatio.vue';

  import { reactive, UnwrapRef, onMounted, ref } from 'vue';
  import { getTopInfoData } from './data';
  import { assign } from 'lodash-es';
  import { buildUUID } from '@/utils/uuid';

  //搜索参数
  const monitorSearchInfo: UnwrapRef<any> = reactive({});
  const topIndexData: UnwrapRef<any> = reactive({});
  const monitorLoading = ref<boolean>(false);
  const instanceLKey = ref(buildUUID());
  const instanceTKey = ref(buildUUID());
  const instanceRKey = ref(buildUUID());

  interface Props {
    searchStationInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});

  // 获取顶端指标函数
  const queryTopInfoData = async () => {
    let data = await getTopInfoData(monitorSearchInfo);
    if (data.result) {
      monitorLoading.value = true;
      assign(topIndexData, data.result);
    }
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceLKey.value = buildUUID();
    instanceTKey.value = buildUUID();
    instanceRKey.value = buildUUID();
  };

  defineExpose({
    handleUpdate,
  });

  onMounted(() => {
    assign(monitorSearchInfo, props.searchStationInfo);
    // 顶端指标
    queryTopInfoData();
  });
</script>

