  .right-content {
    display: flex;
    position: absolute;
    top: 0.9rem;
    right: 0;
    flex-direction: column;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.68vw 1.68vh 0.57vw;
  }

  .section-box {
    flex: 1;
